





























import { Vue, Component, Prop, Watch } from 'vue-property-decorator';

import { Tree } from 'element-ui';

import debounce from 'lodash.debounce';

@Component({
    name: 'MySearchableTree'
})
export default class MySearchableTree extends Vue {
    /** 树形结构列表 */
    @Prop({
        type: Array,
        default: () => new Array()
    })
    treeList!: any[];

    @Prop({
        type: Boolean,
        default: false
    })
    expandOnClickNode!: Boolean[];

    /** 树形结构数据原型 */
    @Prop({
        default: () => {
            return {
                children: 'children',
                label: 'label'
            };
        }
    })
    treeProps!: any;

    /** 选中的节点 */
    @Prop({
        default: () => new Object()
    })
    selectedNode!: any;

    /** 节点key属性 */
    @Prop({
        default: 'id'
    })
    nodeKey!: string;

    /** 输入框占位文字 */
    @Prop({
        default: '输入检索'
    })
    searchInputPlaceholder!: string;

    /** 检索内容 */
    search = '';

    /** -------当前选中节点的计算属性------ */
    get currentSelectedNode() {
        return this.selectedNode;
    }

    set currentSelectedNode(val) {
        this.emitUpdate(val);
    }
    // ---------------------------------------

    // el-tree组件
    get tree() {
        return this.$refs.tree as Tree;
    }

    emitUpdate = debounce(
        function(this: MySearchableTree, val: any) {
            this.$emit('update:selected-node', val);
        },
        500,
        { leading: true }
    );

    // 节点点击处理事件
    handleNodeClick(data: any) {
        // Reflect.deleteProperty(data, this.treeProps.children);
        this.currentSelectedNode = data;
    }

    // 节点筛选方法
    filterNode(value: string, data: any) {
        if (!value) return true;
        return data[this.treeProps.label].indexOf(value) >= 0;
    }

    @Watch('search')
    watchSearch(newVal: string) {
        (this.$refs.tree as Tree).filter(newVal);
    }
}
